export function reqPublicReply (params,data) {
    // console.log("",params,data);
    return new Promise((resolve, reject) => {
        request({
            url: '/reply/publishReply',
            method: 'post',
            params,
            data
        }).then((res)=>{
            // console.log("",res);
            Message({ text: res.msg, type: 'success' })
            resolve()
        }).catch((err)=>{
            console.log("",err);
            Message({ text: err.msg, type: 'warn' })
        })
    })
}