export default {
  install(Vue) {
    // 
    Vue.directive("lazy", (el, binding) => {
      // console.log("",binding);
      let oldSrc = el.src; //srcsrc
      el.src = ""; //src
      let observer = new IntersectionObserver(
        ([{ isIntersecting }]) => {
          // elDOM
          if (isIntersecting) {
            //true or false
            // el.src = oldSrc; //DOM
            // console.log(666);
            el.src = binding.value
            observer.unobserve(el); // 
          }
        },
        { threshold: 0 }
      ); // 
      observer.observe(el); // 
    });
  },
};
