import axios from "axios";
//
import nprogress from "nprogress";
import router from "../router/router.ts"
import Message from "../../packages/message/src/index"
const service = axios.create({
  baseURL: "/wcy",
  // baseURL: 'http://123.249.125.223:8080/wcy',
  // baseURL: 'https://fastmock.site/mock/a6bf479aab19dd7f40fc10522cdead00/wcy', // url = base url + request url
  // withCredentials: true, // send cookies when cross-domain requests
  timeout: 8000, // request timeout
});

// request interceptor
service.interceptors.request.use(
  (config) => {
    // do something before request is sent

    // token
    // if (store.getters.token) {
    //   // let each request carry token
    //   // ['X-Token'] is a custom headers key
    //   // please modify it according to the actual situation
    //   config.headers['X-Token'] = getToken()
    // }
    // 
    // console.log("",config);
    nprogress.start();
    return config;
  },
  (error) => {
    // do something with request error
    console.log("", error); // for debug
    console.log("404");
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  /**
   * If you want to get http information such as headers or status
   * Please return  response => response
   */

  /**
   * Determine the request status by custom code
   * Here is just an example
   * You can also judge the status by HTTP Status Code
   */
  (response) => {
    // 
    nprogress.done();

    const res = response.data;
    //console.log("",res) // for debug
    // if the custom code is not 20000, it is judged as an error.
    // 2000
    if (res.code != "200") {
      // if (res.code == "401") {
      //   console.log("", res);
      //   return
      // }
      // Message({
      //   message: res.msg || 'Error',
      //   type: 'error',
      //   duration: 5 * 1000
      // })
      // console.log("");
      return Promise.reject(res);
    } else {
      //console.log("");
      return Promise.resolve(res);
    }
  },
  (error) => {
    // console.log("",error) // for debug
    // console.log("404");
    // console.log(error.response.data.code=="401");
    nprogress.done();
    if (error.response.data.code == "401") {
      Message({
        text: "",
        type: "error"
      });
      router.push('/login')
    }
    // console.log('err' + error) // for debug
    // Message({
    //   message: error.message,
    //   type: 'error',
    //   duration: 5 * 1000
    // })
    return Promise.reject(error);
  }
);

export default service;
